import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home';
import codePage from '@/components/codePage';
import codePageExcept from '@/components/codePageExcept';
import isolationTime from '@/components/isolationTime'
import passableCertificate from '@/components/passableCertificate'
import toBeRetested from '@/components/toBeRetested'
import result from '@/components/result';
import operation from '@/components/operation';

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'codePage',
      component: codePage
    },
    {
      path: '/codePage',
      name: 'codePage',
      component: codePage
    }, 
    {
      path: '/codePageExcept',
      name: 'codePageExcept',
      component: codePageExcept
    }, 
    {
      path: '/isolationTime',
      name: 'isolationTime',
      component: isolationTime // 4 未过隔离时间
    },
    {
      path: '/passableCertificate', 
      name: 'passableCertificate',
      component: passableCertificate // 7 可通行凭证
    },
    {
      path: '/toBeRetested', 
      name: 'toBeRetested',
      component: toBeRetested // 8 体温异常待复测
    },
    {
      path: '/result',
      name: 'result',
      component: result
    },
    {
      path: '/operation',
      name: 'operation',
      component: operation
    }
  ]
})



