<template>

  <div class="zhanyi_home">
    <div class="kong"></div>
    <div class="zhanyi_main">
    <div class="zhanyi_top">
      <img src="../../assets/banner.png" alt="">
    </div>
    <div class="user_information">
      <div class="user_kong"></div>
      <div class="user_name">
        <div class="user_name_top">姓名</div>
        <div class="user_name_bottom">{{name}}</div>
      </div>
      <div class="user_name">
        <div class="user_kong"></div>
        <div class="user_name_top">手机号</div>
        <div class="user_name_bottom">{{telephone}}</div>
      </div>
      <div class="user_time">
        <div class="user_time_kong"></div>
        <div class="user_time_top">
          <span>返回工作地点</span>
          <span class="nogo">未离开</span>
          <img v-if="nogo" src="../../assets/yixuan.png" alt=""  @click="handleGoback">
          <span  v-if="!nogo" class="noimg" @click="handleGoback"></span>
        </div>
        <div class="user_time_bottom" @click="onChangePicker()">{{userTime}}</div>
          <mt-datetime-picker
          ref="pickerData" 
          v-model="pickerVisible"
          type="date"
          :startDate="new Date('2020-01-01')"
          :endDate="new Date('2020-12-01')"
           year-format="{value} 年"
          month-format="{value} 月"
          date-format="{value} 日"
          @confirm="handleConfirm">
        </mt-datetime-picker>

      </div>
    </div>
    </div>
    <!-- <div class="message_hint">
      为了你和同事的健康，请务必如实填写以上信息，故意隐瞒误报将被追究法律责任
    </div>

    <div :class="btn"  @click="next">
      {{nextText}}
    </div> -->
    
  </div>

</template>

<script>
import './index.css';
import { MessageBox } from 'mint-ui';
import { getUser,userSave } from '@/server/home.js'
export default {
  data() {
    return {
      name:'',
      telephone:'',
      pickerVisible:undefined,
      userTime:'请选择',
      userDate:undefined,
      btn:'user_button',
      nextText:'下一步',
      nogo:false
    }
  },
  async mounted() {

    return;

    let res = await getUser()
    console.log(res)

    if( res.data.code == 200 ){
       localStorage.setItem("userName",res.data.data.realname);
       localStorage.setItem("healthStatue",res.data.data.healthStatue);
       localStorage.setItem("normalDate",res.data.data.normalDate);
       localStorage.setItem("days",res.data.data.days);
       localStorage.setItem("qrcode",res.data.data.qrcode);
       localStorage.setItem("retryNum",res.data.data.retryNum);
       localStorage.setItem("tryAgain",res.data.data.tryAgain);
       localStorage.setItem("tryAgainRemaining",res.data.data.tryAgainRemaining);
       this.name = res.data.data.realname;

        if (res.data.data.isfillbacktime == '0') {
          this.$router.replace('/isolationTime');
          return;
        }

        // 老版本 未满足隔离时间
        // if( ('checkresult' in res.data.data)  && (res.data.data.checkresult == '1')){
        //   this.$router.replace('/isolationTime')
        //   return;
        // }

        if ('isMeasuring' in res.data.data) {
          if(res.data.data.isMeasuring  == false) {
            this.$router.replace('/codePage');
            return;
          }
          
          // 当天通过
          if((res.data.data.isMeasuring  == true) && (res.data.data.measuringResult == true)){
            this.$router.replace('/passableCertificate');
            return;
          }

          // 复测
          if((res.data.data.isMeasuring  == true) && (res.data.data.measuringResult == false)){
            this.$router.replace('/codePageExcept');
            return;
          }
        }

        this.$router.replace('/codePage');
    }else{
      MessageBox('提示', res.data.msg);
    }
  },
  methods: {
    handleConfirm(time){
      let day = new Date()
  if( time.getTime() > day.getTime() ){
    MessageBox('提示', '不可选择超过当天的时间');
    this.userTime = '请选择'
  }else{
      this.userDate = this.$moment(time).format('YYYY.MM.DD')
      this.userTime = this.$moment(time).format('YYYY.MM.DD')
      if(this.userDate != undefined){
        this.btn = 'btn_active' 
         this.nextText = '确认'
      } else{
        this.btn = 'user_button'
         this.nextText = '下一步'
      } 
    }
    },
    onChangePicker(){
      console.log(this.$refs.pickerData)
      this.$refs.pickerData.open(); 
      // this.pickerVisible = true
    },
    async next(){
      if( this.btn == 'btn_active' ){
        // todo跳转
       localStorage.setItem("time",this.userDate);
        let param = {
          backtime:this.userDate
        }
        let res = await userSave(param)
        if( res.data.code == 200 ){
          localStorage.setItem("statue",res.data.data.statue);
          localStorage.setItem("normalDate",res.data.data.normalDate);
          localStorage.setItem("qrcode",res.data.data.qrcode);
          localStorage.setItem("days",res.data.data.days);
          if( res.data.data.statue  == 0 ){
            this.$router.push('/codePage')
          }else{
            this.$router.push('/isolationTime')
          }
        }else{
          MessageBox('提示', res.data.msg);
        }
        
      }else {

      }
    },
    handleGoback(){
      this.nogo = !this.nogo
      if( this.nogo == true ){
        this.userTime = '未离开'
        this.btn = 'btn_active' 
         this.nextText = '确认'
         this.userDate = "未离开"
      }else {
        this.btn = 'user_button'
        this.userTime = '请选择'
         this.nextText = '下一步'
         this.userDate = undefined
      }
    }
  }
}
</script>





