<template>
    <div class="isolationTime-page">
        <div class="container">
            <div class="user-info">
                <div class="user-name">{{userName}}</div>
                <div class="sign">体温异常</div>
            </div>
            <div class="return-date">
                <div class="date-time">{{minute}}:{{second}}</div>
            </div>
            <div class="no-pass">体温异常，请在5分钟后复测</div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                minutes: 5,
                seconds: 0,
                userName: '',
            }
        },
        mounted () {
            this.userName = localStorage.getItem('userName')
            this.add()
        },
        methods: {
            num: function (n) {
                return n < 10 ? '0' + n : '' + n
            },
            add: function () {
                var _this = this
                var time = window.setInterval(function () {
                    if (_this.seconds === 0 && _this.minutes !== 0) {
                        _this.seconds = 59
                        _this.minutes -= 1
                    } else if (_this.minutes === 0 && _this.seconds === 0) {
                        _this.seconds = 0
                        window.clearInterval(time)
                        _this.$router.replace('/codePageExcept')
                    } else {
                        _this.seconds -= 1
                    }
                }, 1000)
            }
        },
        watch: {
            second: {
                handler (newVal) {
                    this.num(newVal)
                }
            },
            minute: {
                handler (newVal) {
                    this.num(newVal)
                }
            }
        },
        computed: {
            second: function () {
                return this.num(this.seconds)
            },
            minute: function () {
                return this.num(this.minutes)
            }
        }
    }
</script>

<style scoped>
.isolationTime-page{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background:rgba(246,247,251,1);
    padding: 20px;
    font-size: 14px;
    box-sizing: border-box;
}
.container{
    background: #fff;
    box-sizing: border-box;
    width: 100%;
    box-shadow:0px 5px 20px 0px rgba(51,119,255,0.05);
    border-radius:10px;
    padding: 32px 28px 58px;
    position: relative;
}
/* .user-info {
    background: #fff;
    padding: 20px;
} */
.user-info .user-name{
    font-size:24px;
    font-family:PingFangSC-Medium,PingFang SC;
    font-weight:500;
    color:rgba(51,51,51,1);
    line-height:28px;
    margin-bottom: 14px;
}
.user-info .sign{
    font-size:16px;
    font-family:PingFangSC-Regular,PingFang SC;
    font-weight:400;
    /* color:rgba(153,153,153,1); */
    color: #FF5319;
    line-height:16px;
}
.no-pass{
    font-size:14px;
    font-family:PingFangSC-Regular,PingFang SC;
    font-weight:400;
    color:rgba(255,83,25,1);
    line-height:14px;
    text-align: center;
}
.return-date {
    width: 200px;
    height: 200px;
    margin: 40px auto 28px;
    border-radius: 50%;
    background:linear-gradient(145deg,rgba(255,121,77,1) 0%,rgba(255,83,25,1) 100%);
    box-shadow:2px 4px 20px 0px rgba(255,84,27,0.5);
    padding-top: 76px;
    box-sizing: border-box;
}
.return-date .date-time {
    text-align: center;
    font-size:56px;
    font-family:don58-Regular,don58;
    font-weight:400;
    color:rgba(255,255,255,1);
    line-height:56px;
}
</style>


