<template>
    <div class="isolationTime-page">
        <div class="container">
            <div class="user-info">
                <div class="user-name">{{userName}}</div>
                <div class="sign">“欢迎回公司，我们一起加油!”</div>
                <div class="thermometry-logo"><i class="thermometer"></i> <b>今日已测温</b></div>
            </div>
            <div class="return-date">
                <div class="date-time">{{dateTime}}</div>
                <div class="date-info">可通行</div>
            </div>
            <div class="no-pass">已满足复工条件</div>
        </div>
        <div class="enter_code" @click="goToCode">
          <img src="../../assets/enter_code@2x.png"/>
          <span>测温二维码</span>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                userName: '张三',
                dateTime: ''
            }
        },
        mounted () {
            // this.userName = localStorage.getItem('userName')
            this.userName = '张三'
            let myDate = new Date(),
                year = myDate.getFullYear(),
                month = myDate.getMonth()+1<=9 ? '0'+(myDate.getMonth()+1) : (myDate.getMonth()+1),
                day = myDate.getDate()<=9 ? '0'+myDate.getDate() : myDate.getDate();
            this.dateTime = year + '.' + month + '.' + day;
        },

        methods: {
          goToCode() {
            this.$router.replace('/codePage');
          }
        }
    }
</script>

<style scoped>
.isolationTime-page{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background:rgba(246,247,251,1);
    padding: 20px;
    font-size: 14px;
    box-sizing: border-box;
}
.container{
    background: #fff;
    box-sizing: border-box;
    width: 100%;
    box-shadow:0px 5px 20px 0px rgba(51,119,255,0.05);
    border-radius:10px;
    padding: 32px 28px 58px;
    position: relative;
}
/* .user-info {
} */
.user-info .user-name{
    font-size:24px;
    font-family:PingFangSC-Medium,PingFang SC;
    font-weight:500;
    color:rgba(51,51,51,1);
    line-height:28px;
    margin-bottom: 14px;
}
.user-info .sign{
    font-size:16px;
    font-family:PingFangSC-Regular,PingFang SC;
    font-weight:400;
    color:rgba(153,153,153,1);
    line-height:16px;
}
.no-pass{
    font-size:14px;
    font-family:PingFangSC-Regular,PingFang SC;
    font-weight:400;
    color:rgba(153,153,153,1);
    line-height:14px;
    text-align: center;
}
.user-info .thermometry-logo{
    font-size: 14px;
    height: 32px;
    /* padding: 0 10px; */
    padding-left: 5px;
    padding-right: 15px;
    position: absolute;
    right: 0;
    top: 30px;
    background:rgba(18,208,151,0.1);
    border-radius:17px 0px 0px 17px;
    box-sizing: border-box;
}
.thermometry-logo b {
    font-size:16px;
    font-family:PingFangSC-Regular,PingFang SC;
    font-weight:400;
    color:rgba(0,196,180,1);
    line-height: 10px;
    position: relative;
    right: 0;
    top: -4px;
}
.thermometer{
    position: relative;
    top: 3px;
    display: inline-block;
    width: 26px;
    height: 26px;
    background: url(../../assets/thermometer.png) 0px center /100% 100% no-repeat;
    background-size: contain;
}
.return-date {
    width: 200px;
    height: 200px;
    margin: 40px auto 28px;
    border-radius: 50%;
    background:linear-gradient(141deg,rgba(74,230,178,1) 0%,rgba(0,191,191,1) 100%);
    box-shadow:2px 4px 20px 0px rgba(1,192,176,0.3);
    padding-top: 60px;
    box-sizing: border-box;
}
.return-date .date-time {
    text-align: center;
    font-size:26px;
    font-family:don58-Regular,don58;
    font-weight:400;
    color:rgba(255,255,255,1);
    line-height:26px;
    margin-bottom: 10px;

}
.return-date .date-info {
    text-align: center;
    font-size:45px;
    font-family:PingFangSC-Medium,PingFang SC;
    font-weight:500;
    color:rgba(255,255,255,1);
    line-height:45px;
}

.enter_code {
  width: 275px;
  height: 44px;
  line-height: 44px;
  margin: 0 auto;
  margin-top: 40px;
  box-sizing: border-box;
  text-align: center;
  border-radius:22px;
  border: 1px solid #00C4B4;
  font-family:PingFangSC-Medium,PingFang SC;
  font-weight:500;
  color:rgba(0,196,180,1);
  font-size: 0;
}

.enter_code > img {
  width: 16px;
  height: 16px;
  position: relative;
  top: 2px;
}

.enter_code  > span{
   font-size:16px;
   margin-left: 8px;
}



</style>


