import {baseURL} from './config';
import axios from 'axios';


/**
 * 查询用户状态，轮询
 */
export const getUserResult = () => {
  return axios.get(`${baseURL}/usercheck/statue`);
}


/**
 * 用户获取二维码
 * 二维码10分钟有效，需要每10分钟轮询一次
 * {
 *   type: 0, //复测， 1, //仅获取二维码
 * }
 */
export const getUserQrcode = (type) => {
  return axios.get(`${baseURL}/usercheck/qrcode`, {
    params: {
      type
    }
  });
}


