import axios from  'axios';
import qs from 'qs';

axios.interceptors.request.use(
  config => {
      if((config.headers['Content-Type'] !== 'x-www-form-urlencoded') && (config.headers['Content-Type'] !== 'mulitpart/form-data')){
          config.transformRequest = (data)=>{
              data = qs.stringify(data);
              return data
          }
      }
      config.withCredentials = true
          return config
      },function(error){
          return Promise.reject(error)
      }
);
axios.interceptors.response.use(function (response) {
  return response
}, function (error) {
  console.log(error,'error');
  return Promise.reject(error);
});


export const baseURL =  (process.env.NODE_ENV == 'development' ? '/api':  '');


/**
 * @param {api接口} fn 
 * @return [err, data]
 */
export const promiseFnByCatch = (fn) => {
  return async function(...arg) {
    let result = [];
    try {
      result[1] = await fn(...arg);
    } catch(err) {
      result[0] = err;
    }
    return result;
  }
};



