        mounted() {
            // this.userName = localStorage.getItem('userName')
            this.userName = '张三';
            // let normalDate = localStorage.getItem('normalDate')
            let normalDate = '2020年3月12日'
            this.normalDate = normalDate.replace('月', '.').replace('日', '')
        },
        methods: {
            showDialog() {
                this.isDialog = true;
            },
            hideDialog() {
                this.isDialog = false;
            }
        }
