<template>
    <div class="result"> 
        <div class="kong"></div>
        <div class="result_main">
            <!-- <div class="result_kong"></div> -->
            <!-- <div class="result_name">{{name}}</div> -->
            <div class="result_userinfo">
                <div class="result_name">{{name}}</div>
                <div class="result_user">{{user}}</div>
            </div>
            <!-- <div class="result_user">{{user}}</div> -->
            <div class="result_titlt">
                <img src="../../assets/tishi.png" alt="">
                <div class="result_temperature">
                    <span>37.3°C</span>
                    以下为正常体温
                    </div>
            </div>
            <div style="clear:both"></div>
        </div>
            <div class="result_btn_normal" @click="handleNormal">体温正常</div>
            <div class="result_btn_abnormal"  @click="handleAbnormal">体温异常</div>
    </div>
</template>

<script>
import './index.css';
import { MessageBox } from 'mint-ui';
import { getUser,userSave } from '@/server/result'
export default {
    name: '',
    data() {
        return {
            name:'阎皓岚',
            user:'yanhaolan00',
            id:''
        }
    },
    mounted(){
        this.name =  localStorage.getItem("admin_name");
        this.user = localStorage.getItem("admin_user");
        this.id = localStorage.getItem("admin_id");
    },
    methods: {
        async mounted() {
            let qrcode = localStorage.getItem("qrcode")
            let res = await getUser({qrcode:qrcode})
            if( res.data.code == 200 ){
                this.user = res.data.data.realname
                this.name = res.data.data.username
                this.id = res.data.data.id
            }else{
                MessageBox('提示', res.data.msg);
            }
        },
        // 正常
        async handleNormal(){
            let param = {
                id: this.id,
                normal: true
            }
            let res = await userSave(param)
            if( res.data.code == 200 ){
                localStorage.setItem("normal",'体温正常');
                this.$router.push('/operation')
            }else{
                MessageBox('提示', res.data.msg);
            }
        },
        // 异常
        async handleAbnormal(){
            let param = {
                id: this.id,
                normal: false
            }
            let res = await userSave(param)
            if( res.data.code == 200 ){
                localStorage.setItem("normal",'体温异常');
                this.$router.push('/operation')
            }else{
                MessageBox('提示', res.data.msg);
            }
        }
    },


    }
</script>

<style lang="" scoped>
    
</style>


