import {baseURL} from '@/server/config.js';
import axios from 'axios';

// 获取列表
export const getUser = () => {
    // return  axios.get(`${baseURL}/health_check/qrcode/{id}/`);
    return axios.get(`${baseURL}/apimock/3598/health_check/qrcode/{id}/`)
}
// 保存
export const userSave = (param) => {
    // return  axios.get(`${baseURL}/health_check/confirm`);
    return axios.post(`${baseURL}/apimock/3598/health_check/confirm`,param)
}




