<template>
  <div class="scan_code_except">
    <div class="content">
      <p class="content_name">{{this.userName}}</p>
      <p class="content_jiayou">体温异常</p>
      <div class="qrcode_border">
        <div class="qrcode_border_mask" v-if="errorMask">
          <p v-if="timeout"  class="timeout_err">二维码过期</p>
          <p v-else class="load_error">加载失败</p>
          <button @click="refreshQrcode">点击刷新</button>
        </div>
        <!-- <div class="qrcode_border_line"></div> -->
        <div id="qrcode_el" class="content_show_code"></div>
      </div>
      
      <p class="content_geli">请复测体温，第{{this.retryNum}}次</p>
    </div>

    <p class="content_hint">请出示此码,并测量体温</p>

    <!-- <p class="content_fankui">
      <span  @click="handleFankui">问题反馈</span>
    </p> -->

    <div class="scan_code_mask" v-if="visible"></div>
    <div class="scan_code_popup" v-if="visible">
      <P class="scan_code_popup_title">如信息填写有误</p>
      <p class="scan_code_popup_desc">请用美事联系管理员 <span>杨子⻰</span> yangzilong01</p>
      <div class="scan_code_popup_btn" >
        <button @click="handleKnow">知道了</button>
      </div>
    </div>
    
  </div>
  
</template>

<script>
import './index.css';
import {
  getUserResult,
  getUserQrcode
} from '../../server/codePage';
import {promiseFnByCatch} from '../../server/config';

export default {
  data() {
    return {
      userName: '张三',
      visible: false,
      codeTimer: null,
      retryNum: 0, // 复测次数
      resultTimer: null,
      errorMask: false,
      timeout: false, // 二维码过期
    }
  }, 

  created() {
    // this.userName = window.localStorage.getItem('userName');
  },

  mounted() {
    // this.scheduleRefreshHint();
    this.genQrcode();
  },

  beforeDestroy () {
    window.clearTimeout(this.codeTimer);
    window.clearTimeout(this.resultTimer);
    this.codeTimer = null;
    this.resultTimer = null;
  },


  methods: {

    // scheduleGetQrcode () {
    //   this.codeTimer = window.setInterval(() => {
    //     this.genQrcode();
    //   }, 10 * 60 * 1000);
    // },

    // 间隔 60s提示用户刷新二维码
    scheduleRefreshHint () {
      window.clearTimeout(this.codeTimer);
      window.clearTimeout(this.resultTimer);
      this.codeTimer = null;
      this.resultTimer = null;

      this.codeTimer = window.setTimeout(()=> {
        this.errorMask = true;
        this.timeout = true;
        window.clearTimeout(this.resultTimer);
        this.resultTimer = null;
      }, 60 * 1000);
    },

    refreshQrcode () {
      this.scheduleRefreshHint();
      this.timeout = false;
      this.errorMask = false;
      this.genQrcode();
    },


    async genQrcode () {

      let font = parseInt(document.documentElement.style.fontSize) / 75;
      jQuery('#qrcode_el').empty();
      jQuery('#qrcode_el').qrcode({
        render: "canvas", 
        width: 205 * font ,
        height: 205 * font,
        foreground: "#FF5319",
        background: "#fff",
        text: 'hello'
      });


      /*
      let result = await promiseFnByCatch(getUserQrcode)(0);

      if (result[0]) {
        window.clearTimeout(this.codeTimer);
        window.clearTimeout(this.resultTimer );
        this.codeTimer = null;
        this.resultTimer = null;
        this.errorMask = true;
        return;
      }
      this.retryNum = result[1].data.data.retryNum;
     
      let font = parseInt(document.documentElement.style.fontSize) / 75;
      jQuery('#qrcode_el').empty();
      jQuery('#qrcode_el').qrcode({
        render: "canvas", 
        width: 205 * font ,
        height: 205 * font,
        foreground: "#FF5319",
        background: "#fff",
        text: result[1].data.data.qrcode
      });

      try {
          window.MSJSBridge.Call({
            target : "system",
            action : "screenSetting",
            data : {"keepScreenOn":"1","highlight":"1"}
          });
      } catch(err) {}

      this.getResult();
      */
    },

    async getResult () {
      window.clearTimeout(this.resultTimer);
      this.resultTimer = null;

      let result = await promiseFnByCatch(getUserResult)();
      if (result[0]) {
        window.clearTimeout(this.resultTimer);
        this.resultTimer = null;
        this.getResult();
        return;
      }

      if (result[1].data.data) {
        if (result[1].data.data.statue == '0') {
          this.$router.replace('/passableCertificate');
        } else if (result[1].data.data.statue == '1') {
          this.$router.replace('/toBeRetested');
        }else if (result[1].data.data.statue == '2') {
          this.resultTimer = setTimeout(() => {this.getResult();}, 1000);
        }
      } else {
        this.resultTimer = setTimeout(() => {this.getResult();}, 1000);
      }
    },

    handleFankui () {
      this.visible = true;
    },
    handleKnow () {
      this.visible= false;
    }
  }
}
</script>


