import {baseURL} from '@/server/config.js';
import axios from 'axios';

// 获取列表
export const getUser = () => {
    return  axios.get(`${baseURL}/usercheck/userinfo`);
    // return axios.get(`${baseURL}/apimock/3594/usercheck/userinfo`)
}
// 保存
export const userSave = (param) => {
    return  axios.post(`${baseURL}/usercheck/baseinfo/save`,param);
    // return axios.post(`${baseURL}/apimock/3595/usercheck/baseinfo/save`,param)
}




