<template>
    <div class="operation"> 
        <div class="kong"></div>
        <div class="operation_main">
            <!-- <div class="operation_kong"></div> -->
            <!-- <div class="operation_name">{{name}}</div> -->
            <div class="operation_userinfo">
                <div class="operation_name">{{name}}</div>
                <div class="operation_user">{{user}}</div>
            </div>
            <!-- <div class="operation_user">{{user}}</div> -->
            <div class="operation_titlt">
                <img src="../../assets/jinggao.png" alt="">
                <div class="operation_temperature">
                    <span>体温异常</span>
                    </div>
            </div>
            <div style="clear:both"></div>
        </div>
            <div class="operation_btn_normal" @click="continues">继续扫码</div>
            <div class="operation_btn_abnormal"  @click="goBack">返回修改</div>
    </div>
</template>

<script>
import './index.css';
import { MessageBox } from 'mint-ui';
import { getUser,userSave } from '@/server/result'
export default {
    name: '',
    data() {
        return {
            name:'阎皓岚',
            user:'yanhaolan00',
            temperature:'40.3',
            title:''
        }
    },
    async mounted() {
        this.title = localStorage.getItem("normal")
        let qrcode = JSON.parse(localStorage.getItem("qrcode"))
        let res = await getUser({qrcode:qrcode})
        if( res.data.code == 200 ){
            this.user = res.data.data.realname
            this.name = res.data.data.username
            this.id = res.data.data.id
        }else{
            MessageBox('提示', res.data.msg);
        }
    },
    methods: {
        goBack(){
            this.$router.back(-1)
        },
        continues(){
            window.MSJSBridge.Call({
                target : "qrcode",
                action : "scanResult",
                successCallback:{
                    result:function(res){
                        console.log(res)
                        // 扫码
                        getUser(res).then( ele=> {
                            console.log(ele)
                            if( res.data.code == 200 ){
                                let qrcode = JSON.parse(localStorage.getItem("qrcode"))
                                localStorage.setItem("admin_name",res.data.data.username);
                                localStorage.setItem("admin_user",res.data.data.realname);
                                localStorage.setItem("admin_id",res.data.data.id);
                                this.$router.push('/result')
                            }else{
                                MessageBox('提示', res.data.msg);
                            }
                        } )
                    }
                }
            });

        }
    },
}
</script>

<style lang="" scoped>
    
</style>


